}
.seal_small:before {
  padding-top: 100%;
}
.seal_small > span.alt-text:before {
  content: "Seal of the American College Of Cardiology";
}

.seal_tiny {
  background-image: url("../img/ACC_sealCondensed-alt.svg");
  max-width: 40px;
}
.seal_tiny:before {
  padding-top: 100%;
}
.seal_tiny > span.alt-text:before {
  content: "Seal of the American College Of Cardiology";
}

