div[class*=aspect_],
.lorem-loader {
  width: 100%;
  overflow: hidden;
  text-indent: -9999px;
  opacity: 0.5;
}
div[class*=aspect_]:before,
.lorem-loader:before {
  content: "";
  width: 1px;
  margin-left: -1px;
  float: left;
  height: 0;
}
div[class*=aspect_]:after,
.lorem-loader:after {
  content: "";
  display: table;
  clear: both;
}

.aspect_2x1 {
  padding-top: 200%;
