.carousel-alt {
  max-width: 630px;
  position: relative;
  background: url("http://cdn.backgroundhost.com/backgrounds/subtlepatterns/subtle_surface.png");
  background-repeat: repeat;
  margin: 0 auto 1rem;
}
.carousel-alt:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 66.6666666667%;
}
.carousel-alt > .carousel-inner {
  position: absolute;
  top: 0px;
  right: 0;
  bottom: 0;
  left: 0;
}
.carousel-alt .carousel-indicators {
  background-color: rgba(0, 0, 0, 0.6);
  bottom: auto;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  margin: 0;
}
.carousel-alt .carousel-indicators li {
  text-indent: -9999px;
  overflow: hidden;
  border: none;
  border-radius: 25px;
  box-sizing: content-box;
  width: 12px;
  height: 12px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.carousel-alt .carousel-indicators li.active {
  border-color: get-themcolor(accent);
  background-color: get-themcolor(accent);
}
.carousel-alt.chevron-on .carousel-indicators li.active:before {
  content: "";
  color: #c34198;
  font-size: 2.0736em;
  padding-top: 5px;
  top: 4px;
  left: 4px;
}
@media only screen and (min-width : 640px) {
  .carousel-alt.text-indicators {
    max-width: 900px;
  }
